export default {
	props: {
		value: [Number, String],
		type: { // 传值
			type: Array,
			default: ()=> {
				return []
			}
		},
		itemColor: String, // tab主色调
		lineColor: String // 下划线主色调
	},
	data() {
		return {
			currentIndex: 0,
			lineStyle: {},
			scrollLeft: 0,
			duration: 0.3
		}
	},
	watch: {
		value() {
			this.currentIndex = this.value
		}
	},
	mounted() {
		this.currentIndex = this.value
		this.setLine()
		this.scrollIntoView()
	},
	methods: {
		select(item, index) {
			this.currentIndex = index
			this.setLine()
			this.scrollIntoView()
			this.$emit('input', this.currentIndex)
		},
		setLine() {
			let lineWidth = 0, lineLeft = 0
			this.getElementData(`#tab_item`, (data)=> {
				let el = data[this.currentIndex]
				lineWidth = el.width / 2
				// lineLeft = el.width * (this.currentIndex + 0.5)  // 此种只能针对每个item长度一致的
				lineLeft = el.width / 2 + (-data[0].left) + el.left
				this.lineStyle = {
					width: `${lineWidth}px`,
					transform: `translateX(${lineLeft}px) translateX(-50%)`,
					transitionDuration: `${this.duration}s`
				};
			})
		},
		scrollIntoView() {  // item滚动
			let count = 0;
			let frames = 30;
			let lineLeft = 0;
			let self = this
			
			this.getElementData('#tab_list', (data)=> {
				let list = data[0]
				this.getElementData(`#tab_item`, (data)=> {
					let el = data[this.currentIndex]
					// lineLeft = el.width * (this.currentIndex + 0.5) - list.width / 2 - this.scrollLeft
					lineLeft = el.width / 2 + (-data[0].left) + el.left - list.width / 2 - this.scrollLeft
					animate();
				})
			})
			
			function animate() {
				self.scrollLeft += lineLeft / frames;
			    if (++count < frames) {
					setTimeout(animate, self.duration / frames);
				}
			}
		},
		getElementData(el, callback){
			uni.createSelectorQuery().in(this).selectAll(el).boundingClientRect().exec((data) => {
				callback(data[0]);
			});
		},
		scroll(e) {
			this.scrollLeft = e.detail.scrollLeft;
		}
	}
}
